<template>
  <div class="welcome-area-container">
    <h2>迎宾区域</h2>
    <div class="welcome-area-wrapper">
      <div class="staff-wrapper">
        <div class="title">
          值班员工
        </div>
        <div class="infos">
          <div class="staff">
            <div class="staff-info">
              <img :src="staff.img" alt="">
              <div class="info">
                <div>姓名: {{ staff.name }}</div>
                <div>工号: {{ staff.age }}</div>
              </div>
            </div>
            <div class="time">
              <span>状态:</span>
              <span>{{ staff.status }}</span>
            </div>
            <div class="time">
              <span>离岗时间上限:</span>
              <form action="" @submit.prevent="submitLeaveTime"
              >
                <input
                    v-model="staff.leaveTimeLimit"
                    type="text"
                />分钟
              </form>
            </div>
          </div>
        </div>
      </div>
      <div class="leave-time-chart-wrapper">
        <LineChart
            :chart-data="chartData"
            :options="options"
            class="leave-time-chart"
        />
      </div>
    </div>
  </div>
</template>

<script>
import LineChart from "@/components/Charts/LineChart";
import {server} from "../../../config";

export default {
  name: "WelcomeArea",
  components: {LineChart},
  data() {
    return {
      staff: {
        img: 'data:image/png;base64,',
        name: '张三',
        age: '20201215',
        status: '...',
        leaveTimeLimit: 20
      },
      ws: null,
      chartData: {
        labels: Array.from({length: 9}).map((_, i) => 9 + i),
        datasets: [
          {
            label: '无 - 离岗时间统计',
            fill: false,
            backgroundColor: '#000',
            borderColor: '#000',
            data: Array.from({length: 9}).map((_, i) => {
              return Math.round(Math.random() * 100);
            })
          }
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  mounted() {
    this.openWebSocket();
  },
  beforeDestroy() {
    this.ws && this.ws.close(1000, 'manager status destroy');
  },
  methods: {
    openWebSocket() {
      this.ws = new WebSocket(server().ws.managerStatus);
      this.ws.onopen = () => console.log('open');
      this.ws.onmessage = (e) => {
        const data = JSON.parse(e.data);
        console.log(data);
        this.staffs[0].status = data['status'];
      }
      this.ws.onclose = () => console.log('close');
      this.ws.onerror = (error) => console.log(error);
    },
    submitLeaveTime() {
      this.axios.get(server().http.setLeaveTime, {params: {leaveTime: this.staff.leaveTimeLimit}})
          .then((res) => {
            console.log(res);
            this.$message.success('离岗时间上限成功被设置为' + this.staff.leaveTimeLimit);
          })
          .catch((e) => {
            console.log(e);
            this.$message.error('离岗时间上限设置失败');
          })
    }
  }
}
</script>

<style lang="scss" scoped>
.welcome-area-container {
  color: #ffffff;
  padding: 0.5rem;
  margin: 0 0.5rem 0.5rem 0;
  background: #061123;
  border-radius: 5px;
  height: 50%;
}

.welcome-area-container > h2 {
  margin: 0;
}

.welcome-area-wrapper {
  display: flex;
  height: calc(100% - 32px);
  justify-content: space-between;
  padding: 10px;
  box-sizing: border-box;
}

.staff-wrapper {
  margin: 0.5rem;
}

.staff-wrapper .title {
  margin: 0.5rem 0;
}

.staff-info {
  display: flex;
  height: 50px;
}

.staff-info img {
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.staff-info .info {
  display: flex;
  flex-direction: column;
  height: 50px;
  justify-content: space-around;
  margin-left: 0.5rem;
}

.staff .time:nth-child(2) {
  margin: 0.875rem 0;
}

.staff {
  .time {
    > span:first-child {
      width: 110px;
      display: inline-block;
      text-align: right;
      padding-right: 0.5rem;
    }

    > span:last-child {
      display: inline-block;
      width: 60px;
      box-sizing: border-box;
      text-align: center;
      background: #4BC0C0;
      padding: 0.5rem;
    }

    > form {
      display: inline-block;

      input {
        display: inline-block;
        width: 60px;
        box-sizing: border-box;
        padding: 0.5rem;
      }
    }
  }
}

.leave-time-chart-wrapper {
  height: 100%;
}

.welcome-area-wrapper .leave-time-chart {
  height: 100%;
  background: #ffffff;
}
</style>